import { Plugin, constants, transferUtils, CangjieClipboardEvent } from '@ali/4ever-cangjie';
import logger from '@ali/4ever-logger';
import filterFragment from '../util/filterFragment';

const { MIME_TYPES } = constants;


export default function createOnCangjieCopy(): Plugin['onCangjieCopy'] {
  return function onCangjieCopyOrCut(event: CangjieClipboardEvent, _, next) {
    if (!event.clipboardData) {
      return next();
    }

    try {
      const encodedFragment = event.clipboardData.getData(MIME_TYPES.FRAGMENT);
      const fragment = transferUtils.decodeFragment(encodedFragment);

      const newFragment = filterFragment(fragment);
      event.clipboardData.setData(
        MIME_TYPES.FRAGMENT,
        transferUtils.encodeFragment(newFragment),
      );
    } catch (error) {
      logger.error(error);
    }
    return next();
  };
}
